import { owner } from './variables';

import type { Metadata } from 'next';
import type { Author } from 'next/dist/lib/metadata/types/metadata-types';

export interface BaseMetaDateProps
{
	/**
	 * 기본 URL
	 */
	baseurl: string;

	/**
	 * 사이트명
	 */
	sitename: string;

	/**
	 * 타이틀
	 */
	title?: string;

	/**
	 * 설명
	 */
	description?: string;

	/**
	 * 키워드
	 */
	keywords?: string[];

	/**
	 * 경로
	 */
	url?: string;

	/**
	 * 썸네일 url
	 */
	thumbnail?: string;

	/**
	 * 파비콘 url
	 */
	favicon?: string;
}

export interface RssInfoProps
{
	/**
	 * 제목
	 */
	title: string;

	/**
	 * 내용
	 */
	description: string;

	/**
	 * 링크
	 */
	link: string;
}

export interface RssItemProps
{
	/**
	 * 제목
	 */
	title: string;

	/**
	 * 설명
	 */
	description: string;

	/**
	 * 발행일자
	 */
	pubDate: string;

	/**
	 * 링크
	 */
	link: string;

	/**
	 * 카테고리
	 */
	category?: string[];
}

/**
 * 기본 메타데이터 반환 메서드
 *
 * @param {BaseMetaDateProps} param0: BaseMetaDateProps
 *
 * @returns {Metadata} 메타데이터
 */
export function getBaseMetaData({ baseurl, sitename, title, description, keywords, url, thumbnail, favicon = '/favicon.ico' }: BaseMetaDateProps): Metadata
{
	let fullTitle = sitename;

	// 타이틀이 유효할 경우
	if (title !== undefined)
	{
		fullTitle = `${title} | ${fullTitle}`;
	}

	return {
		applicationName: sitename,
		authors: Object.values(owner.social)
			.map<Author>(({ link, name }) => ({
				name,
				url: link
			})),
		creator: owner.nickname,
		description,
		generator: 'Next.js 15',
		icons: [
			favicon,
			{
				rel: 'shortcut icon',
				url: favicon
			},
			{
				rel: 'apple-touch-icon',
				url: favicon
			}
		],
		keywords,
		metadataBase: new URL(baseurl),
		openGraph: {
			description,
			images: thumbnail,
			locale: 'ko-KR',
			siteName: sitename,
			title: fullTitle,
			type: 'website',
			url
		},
		publisher: 'GitHub Pages',
		title: fullTitle,
		twitter: {
			description,
			images: thumbnail,
			title: fullTitle
		}
	};
}

/**
 * RSS 반환 메서드
 *
 * @param {RssInfoProps} info: RssInfoProps
 * @param {RssItemProps} list: RssItemProps
 *
 * @returns {string} RSS
 */
export function getBaseRss(info: RssInfoProps, list: RssItemProps[]): string
{
	const item = list.map((i) =>
	{
		let { title } = i;

		title = title.replace(/</g, '&lt;')
			.replace(/>/g, '&gt;')
			.replace(/"/g, '&quot;')
			.replace(/'/g, '&#39;')
			.replace(/&/g, '&amp;');

		const desc = i.description.replace(/</g, '&lt;')
			.replace(/>/g, '&gt;')
			.replace(/"/g, '&quot;')
			.replace(/'/g, '&#39;')
			.replace(/&/g, '&amp;');

		return `
				<item>
					<title>${title}</title>
					<description>${desc}</description>
					<pubDate>${i.pubDate}</pubDate>
					<link>${i.link}</link>
					<guid isPermaLink="true">${i.link}</guid>
					${i.category?.map((j) => `<category>${j}</category>`)
						.join('\n') ?? ''}
				</item>
			`;
	});

	const xmlContent = `
		<rss version="2.0" xmlns:atom="http://www.w3.org/2005/Atom">
			<channel>
				<copyright>Copyright ⓒ ${owner.nickname} 2021.05</copyright>
				<description>${info.description}</description>
				<generator>${info.title}</generator>
				<language>ko-KR</language>
				<lastBuildDate>${new Date()
					.toISOString()}</lastBuildDate>
				<link>${info.link}</link>
				<managingEditor>${owner.email}</managingEditor>
				<pubDate>2021-05-26T23:36:57.000Z</pubDate>
				<title>${info.title}</title>
				<webMaster>${owner.email}</webMaster>
				${item.join('\n')}
			</channel>
		</rss>
		`;

	return xmlContent;
}