import { cn } from '@kapoo/headless-ui-pack/lib/utils';

import type { CSSProperties, SVGProps } from 'react';

export interface WaveProps extends SVGProps<SVGSVGElement>
{
	/**
	 * 채우기 색
	 */
	readonly fillColor?: CSSProperties['fill'];

	/**
	 * 애니메이션 비활성화 여부
	 */
	readonly disabled?: boolean;
}

/**
 * 웨이브 atom 컴포넌트 반환 메서드
 *
 * @param {WaveProps} param0: WaveProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function Wave({ fillColor, disabled, className, ...props }: WaveProps): React.JSX.Element
{
	return (
		<svg
			className={cn( 'mb--2 relative min-h-10 transition duration-300', className)}
			data-component="Wave"
			preserveAspectRatio="none"
			shapeRendering="auto"
			viewBox="0 24 150 28"
			xmlns="http://www.w3.org/2000/svg"
			{...props}
		>
			<defs>
				<path
					d="M-160 44c30 0 58-18 88-18s 58 18 88 18 58-18 88-18 58 18 88 18 v44h-352z"
					id="gentle-wave"
				/>
			</defs>

			<g>
				<use className={cn({ 'animate-move-forever delay-[-2s]': disabled !== true })} fill={fillColor} opacity="0.7" style={{ animationDuration: '7s' }} x="48" xlinkHref="#gentle-wave" y="0" />
				<use className={cn({ 'animate-move-forever delay-[-3s]': disabled !== true })} fill={fillColor} opacity="0.5" style={{ animationDuration: '10s' }} x="48" xlinkHref="#gentle-wave" y="3" />
				<use className={cn({ 'animate-move-forever delay-[-4s]': disabled !== true })} fill={fillColor} opacity="0.3" style={{ animationDuration: '13s' }} x="48" xlinkHref="#gentle-wave" y="5" />
				<use className={cn({ 'animate-move-forever delay-[-5s]': disabled !== true })} fill={fillColor} style={{ animationDuration: '20s' }} x="48" xlinkHref="#gentle-wave" y="7" />
			</g>
		</svg>
	);
}