import { create } from 'zustand';

export interface EditorProps
{
	/**
	 * 미리보기
	 */
	preview: boolean;

	/**
	 * 래핑
	 */
	wrap: boolean;
}

export type SetEditorHandler = (editor: SetHandler<EditorProps>) => void;

export interface EditorStoreProps
{
	/**
	 * 에디터
	 */
	editorState: EditorProps;

	/**
	 * 에디터 할당 메서드
	 */
	setEditorState: SetEditorHandler;
}

export const editorStore = create<EditorStoreProps>((set, get) => ({
	editorState: {
		preview: true,
		wrap: true
	},
	setEditorState: (editorState): void =>
	{
		const newState = typeof editorState === 'function' ? editorState(get().editorState) : editorState;

		set({ editorState: newState });
	}
}));