import { create } from 'zustand';

export type ThemeStoreSetThemeHandler = (theme: SetHandler<ThemeType>) => void;
export type ThemeStoreToggleThemeHandler = () => void;

export interface ThemeStore
{
	/**
	 * 테마
	 */
	themeState: ThemeType;

	/**
	 * 테마 할당 메서드
	 */
	setThemeState: ThemeStoreSetThemeHandler;

	/**
	 * 테마 토글 메서드
	 */
	toggleThemeState: ThemeStoreToggleThemeHandler;
}

export const themeStore = create<ThemeStore>((set, get) => ({
	setThemeState: (themeState): void =>
	{
		const newState = typeof themeState === 'function' ? themeState(get().themeState) : themeState;

		localStorage.setItem('theme', newState);
		set({ themeState: newState });
	},
	themeState: 'light',
	toggleThemeState: (): void =>
	{
		set(({ themeState }) =>
		{
			const newState = (themeState === 'light' ? 'dark' : 'light');

			localStorage.setItem('theme', newState);

			return ({ themeState: newState });
		});
	}
}));