import { create } from 'zustand';

export type RefererStoreSetHandler = (referer: SetHandler<string | undefined>) => void;

export interface RefererStoreProps
{
	/**
	 * 리퍼러
	 */
	refererState?: string;

	/**
	 * 리퍼러 할당 메서드
	 */
	setRefererState: RefererStoreSetHandler;
}

export const refererStore = create<RefererStoreProps>((set, get) => ({
	setRefererState: (refererState): void =>
	{
		const newState = typeof refererState === 'function' ? refererState(get().refererState) : refererState;

		set({ refererState: newState });
	}
}));