/**
 * 타이포그래피 atom 컴포넌트
 *
 * @author RWB
 * @since 2025.02.07 Fri 11:06:06
 */

import { cn } from '@kapoo/headless-ui-pack/lib/utils';

import type { BlockquoteHTMLAttributes, DetailedHTMLProps, HTMLAttributes } from 'react';

export type TypographyBaseVariant = 'h1' | 'h2' | 'h3' | 'h4' | 'blockquote' | 'inline-code' | 'lead' | 'large' | 'small' | 'muted';
export type TypographyHeadProps = DetailedHTMLProps<HTMLAttributes<HTMLHeadingElement>, HTMLHeadingElement>;
export type TypographyBlockquoteProps = DetailedHTMLProps<BlockquoteHTMLAttributes<HTMLQuoteElement>, HTMLQuoteElement>;
export type TypographyParagraphProps = DetailedHTMLProps<HTMLAttributes<HTMLParagraphElement>, HTMLParagraphElement>;
export type TypographyNormalProps = DetailedHTMLProps<HTMLAttributes<HTMLParagraphElement>, HTMLParagraphElement>;
export type TypographyLargeProps = DetailedHTMLProps<HTMLAttributes<HTMLDivElement>, HTMLDivElement>;

export interface TypographyBaseProps
{
	// 종류
	readonly variant?: TypographyBaseVariant;
}

export type TypographyProps = TypographyBaseProps & (
	| { variant: 'h1' | 'h2' | 'h3' | 'h4' } & TypographyHeadProps
	| { variant: 'blockquote' } & TypographyBlockquoteProps
	| { variant?: 'lead' | 'muted' | undefined } & TypographyParagraphProps
	| { variant: 'inline-code' | 'small' } & TypographyNormalProps
	| { variant: 'large' } & TypographyLargeProps
);

/**
 * 타이포그래피 atom 컴포넌트 반환 메서드
 *
 * @param {TypographyProps} props: TypographyProps
 *
 * @returns {React.JSX.Element} JSX
 */
export default function Typography(props: TypographyProps): React.JSX.Element
{
	switch (props.variant)
	{
		case 'h1': return <h1 data-component="Typography" {...props} className={cn('scroll-m-20 text-4xl font-extrabold tracking-tight lg:text-5xl', props.className)} />;

		case 'h2': return <h2 data-component="Typography" {...props} className={cn('scroll-m-20 border-b pb-2 text-3xl font-semibold tracking-tight first:mt-0', props.className)} />;

		case 'h3': return <h3 data-component="Typography" {...props} className={cn('scroll-m-20 text-2xl font-semibold tracking-tight', props.className)} />;

		case 'h4': return <h4 data-component="Typography" {...props} className={cn('scroll-m-20 text-xl font-semibold tracking-tight', props.className)} />;

		case 'blockquote': return <blockquote data-component="Typography" {...props} className={cn('my-10 border-l-2 pl-6 italic', props.className)} />;

		case 'inline-code': return <code data-component="Typography" {...props} className={cn('bg-muted relative rounded px-[0.3rem] py-[0.2rem] font-["Fira_Code","Pretendard"] text-sm font-semibold select-text', props.className)} />;

		case 'lead': return <p data-component="Typography" {...props} className={cn('text-muted-foreground text-xl', props.className)} />;

		case 'large': return <div data-component="Typography" {...props} className={cn('text-lg font-semibold', props.className)} />;

		case 'small': return <small data-component="Typography" {...props} className={cn('text-sm leading-none font-medium', props.className)} />;

		case 'muted': return <p data-component="Typography" {...props} className={cn('text-muted-foreground text-sm', props.className)} />;

		default: return <p data-component="Typography" {...props} className={props.className} />;
	}
}