/**
 * IntersectionObserver 적용 훅 메서드
 *
 * @param {Element | string | null} ref: Element
 * @param {UseIntersectionObserverCallback} callback: 콜백 메서드
 * @param {IntersectionObserverInit} options: 옵션
 */
export function useIntersectionObserver(ref: Element | string | null, callback: UseIntersectionObserverCallback, options?: IntersectionObserverInit): void
{
	useEffect(() =>
	{
		const io = new IntersectionObserver((entries) =>
		{
			entries.forEach(callback);
		}, options);

		// DOM이 유효할 경우
		if (ref !== null)
		{
			// ref가 문자열일 경우
			if (typeof ref === 'string')
			{
				const tag = document.querySelector(ref);

				// 태그가 유효할 경우
				if (tag)
				{
					io.observe(tag);
				}
			}

			// DOM일 경우
			else
			{
				io.observe(ref);
			}
		}

		return (): void =>
		{
			io.disconnect();
		};
	}, [
		ref,
		callback,
		options
	]);
}

/**
 * 타이핑 훅 반환 메서드
 *
 * @param {(string | string[])[]} prompts: 프롬프트
 * @param {UseTypingOptions} options: 옵션
 *
 * @returns {string} 타이핑 텍스트
 */
export function useTyping(prompts: (string | string[])[], options: UseTypingOptions = useTypingInitOptions): string
{
	const [
		textState,
		setTextState
	] = useState('');

	useEffect(() =>
	{
		let target = prompts[pickIndex(prompts)];
		let idx = 0;
		let forward = true;
		let interval: NodeJS.Timeout;

		const startTyping = (): void =>
		{
			interval = setInterval(() =>
			{
				// 작성일 경우
				if (forward)
				{
					idx++;
				}

				// 텍스트를 지우는 경우
				else
				{
					idx--;
				}

				// 배열일 경우, 조합식으로 동작
				if (Array.isArray(target))
				{
					setTextState(target[idx - 1]);
				}

				// 아닐경우 일반적인 결합으로 동작
				else
				{
					setTextState(target.slice(0, idx));
				}

				// 텍스트를 완전히 썼거나, 완전히 지웠을 경우
				if (idx === target.length || idx === 0)
				{
					clearInterval(interval);

					setTimeout(() =>
					{
						// 텍스트를 완전히 지웠을 경우, 새로운 텍스트를 할당
						if (idx === 0)
						{
							let nextTargetIdx = pickIndex(prompts);

							// 같은 문장을 뽑았을 경우, 다음 문장을 출력
							if (target === prompts[nextTargetIdx])
							{
								nextTargetIdx = modulo(nextTargetIdx + 1, prompts.length);
							}

							target = prompts[nextTargetIdx];
							forward = true;
						}

						// 텍스트를 완전히 썼을 경우, 지우기
						else
						{
							forward = false;
						}

						startTyping();
					}, options.stepDelay);
				}
			}, options.typingDelay);
		};

		startTyping();

		return (): void =>
		{
			clearInterval(interval);
		};
	}, [ options ]);

	return textState;
}